<template>
  <div class="Login-container">
    <!-- 顶部导航 -->
    <van-nav-bar
      title="登录"
      left-text="返回"
      left-arrow
      @click-left="$router.back()"
    />
    <!-- /顶部导航 -->

    <!-- 登录表单 -->
    <!--
      基于Vant的表单验证：
      1、使用van-form组件包裹所有的表单项van-field
      2、给van-form绑定submit事件
        当表单提交的时候会触发submit事件提示：
        只有表单验证通过，它才会调用submit
      3、使用Field的rules属性定义校验规则
     -->
    <van-form @submit="onLogin">
      <van-field
        v-model="user.username"
        icon-prefix="iconfont icon"
        left-icon="shouji"
        name="username"
        placeholder="手机号"
        :rules="formRules.username"
      />
      <van-field
        v-model="user.password"
        icon-prefix="iconfont icon"
        left-icon="yanzhengma"
        type="password"
        name="password"
        placeholder="密码"
        :rules="formRules.password"
      />
      <div style="margin: 16px;">
        <van-button round block type="info" native-type="submit">
          登录
        </van-button>
      </div>
    </van-form>
    <!-- /登录表单 -->
  </div>
</template>

<script>
export default {
  name: 'Login',
  data() {
    return {
      // 表单数据
      user: {
        username: '15651788266',
        password: '123456'
      },
      // 表单验证
      formRules: {
        username: [
          { required: true, message: '请填写手机号' },
          { pattern: /^1[35789]\d{9}$/, message: '手机号格式错误' }
        ],
        password: [
          { required: true, message: '请填写密码' },
          { pattern: /^\d{6}$/, message: '验证码格式错误' }
        ]
      }
    }
  },

  methods: {
    onLogin(user) {
      // console.log('登录成功', user)
      this.$toast.success({
        message: '成功登录',
        type: 'top',
        forbidClick: true // 是否禁止背景点击
      })
      // 将后端返回的用户登录状态（token等数据）放到 Vuex 容器中
      this.$store.commit('setUser', this.user)

      // 清除goodslist的缓存，让它重新渲染
      this.$store.commit('removeCachePage', 'goodsList')

      // 登录成功，跳转回原来页面
      this.$router.back()
    }
  }
}
</script>
<style scoped lang="less"></style>
